<template>
  <div class="popup" v-if="popupVisible">
    <transition name="fade">
      <div class="popup-bg" @click="hide()" v-show="popupVisible"></div>
    </transition>
    <transition name="popup-slide-up">
      <div class="popup-wrapper" v-show="visible">
        <div class="popup-title" v-if="title && title.length > 0">{{title}}</div>
        <div class="popup-byn"
             :class="{'danger': item.type === 'danger'}"
             v-for="(item, index) in btn" :key="index"
             @click="item.click">{{item.text}}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'popup',
    props: {
      title: String,
      btn: Array
    },
    data() {
      return {
        popupVisible: false,
        visible: false
      }
    },
    methods: {
      show() {
        this.popupVisible = true
        setTimeout(() => {
          this.visible = true
        }, 200)
        // this.$nextTick(() => {
        //   this.visible = true
        // })
      },
      hide() {
        this.visible = false
        setTimeout(() => {
          this.popupVisible = false
          return ''
        }, 200)
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped="scoped">
  @import "../../assets/styles/global";
  .popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 2000;
    background: rgba(0, 0, 0, .4);
    .popup-bg {
      width: 100%;
      height: 100%;
    }
    .popup-wrapper {
      position: fixed;
      left: 0;
      bottom: 0;
      z-index: 2000;
      width: 100%;
      background: white;
      .popup-title {
        text-align: center;
        width: 100%;
        height: px2rem(44);
        border-bottom: px2rem(1) solid #eee;
        font-size: px2rem(12);
        line-height: pxz2rem(14);
        padding: px2rem(15);
        box-sizing: border-box;
        color: #999;
        @include center;
      }
      .popup-byn {
        width: 100%;
        height: px2rem(60);
        border-bottom: px2rem(1) solid #eee;
        font-size: px2rem(16);
        color: #666;
        font-weight: bold;
        @include center;
        &.danger {
          color: $color-pink;
        }
      }
    }
  }
</style>
